import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { environment } from '../../environments/environment';
@Injectable({
  providedIn: 'root'
})
export class ApiService {

  constructor(private http: HttpClient) { }

  createTask(payload) {
    return this.http.post(`${environment.url}/tasks`, payload);
  }

  getTasks() {
    return this.http.get(`${environment.url}/tasks`);
  }

  updateTask(id, payload) {
    return this.http.patch(`${environment.url}/tasks/${id}`, payload);
  }

  getTask(id) {
    return this.http.get(`${environment.url}/tasks/${id}`);
  }

  deleteTask(id) {
    return this.http.delete(`${environment.url}/tasks/${id}`);
  }
}
